let log = console.log.bind(console)
export default {
    state: {
        activedName: "首页", //设置也 所选的名字 
        path: "/",  // 当前路由地址
        isShowSetPage: false, // 是否显示 设置页 侧栏
    },
    getters: {

    },
    mutations: {
        // dataObj  {'activedName': true}
        setState(state, dataObj) {
            let keys = Object.keys(dataObj)
            keys.forEach(key => {
                state[key] = dataObj[key]
            })
        },
    },
    actions: {

    }
};