<template>
  <div class="sidebar">
    <div class="sidebar-top">
        <div class="user-img">
            <van-icon name="friends-o" size="50px"/>
            <img src="" alt="" srcset="">
        </div>
        <span class="login-text">未登录</span>
    </div>

    <div class="setting">
        <div class="setting-item" @click="toggleSet(item)"
            v-for="(item, index) in settingObj" :key="index"
            :class="{'active': item.activedName == home.activedName}">
            <van-icon :name="item.icon" size="20px" />
            <span class="setting-content">{{item.activedName}}</span>
        </div>
        <div class="van-hairline--top"></div>
        <div class="setting-item theme">
            
            <span class="setting-content">主题风格</span>
            <div class="toggle">
                <span class="setting-content">夜间模式</span>
                <van-switch v-model="checked" size="12px" />
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import {log} from '../utils/log'
export default {
    name: "sidebar",
    data() {
        return {
            checked: true,
            activedName: '首页',
            settingObj: [
                { icon: 'wap-home-o', activedName: '首页', path: '/home',},
                { icon: 'apps-o', activedName: '频道', path: '/channel',},
                { icon: 'tv-o', activedName: '平台', path: '/platform',},
                { icon: 'like-o', activedName: '我的关注', path: '/youlike',},
                { icon: 'clock-o', activedName: '历史观看', path: '/history',},
                { icon: 'setting-o', activedName: '设置', path: '/setting',},
            ],
        };
    },
    created() {

    },
    computed: {
        ...mapState({
            home: state => state.home
        }),
        // carXianLen() {
        //     return (
        //         this.completed.length +
        //         this.complet.length +
        //         this.processing.length
        //     );
        // }
    },
    methods: {
        ...mapMutations(["setState"]),
        toggleSet(item) {
            let { activedName, path } = item
            log('setName, this.home.setName, click path', this.home.activedName, activedName, path)
            this.activedName = activedName
            this.setState( {'activedName': this.activedName})
            this.setState( {'isShowSetPage': false})
            this.setState( {'path': path})
            this.$router.push(path)  
        },
    },
}
</script>

<style lang="scss" scoped>

    .active {
        background-color: #99d5cf;
        .setting-content {
            color: #037754;
            font-weight: 700;
        }
        .van-icon {
            color: #03976b;
        }
    }
    .user-setting {
        // border-top: 1px solid #dadada;
        // border-bottom: 1px solid #dadada;
    }

    .setting-content {
        color: #888888;
    }

    .sidebar {
        width: 55vw;
        height: 100vh;
        background-color: transparent;
        // background-color: violet;
        .sidebar-top {
            height: 110px;
            background-color: #333333;
            padding: 20px;
            .user-img {
                height: 50px;
                width: 50px;
                border-radius: 50%;
                background-color: #969494;
                margin-bottom: 4px;
            }
            .login-text {
                color: #ffffff;
                font-size: 16px;
            }
        }

        .setting {
            margin-top: 4px;
            // padding: 0 ;
            .setting-item {
                padding: 12px 0;
                // height: 30px;
                // background-color: #cbcbcb;
                display: flex;
                align-items: center;
                .van-icon {
                    margin-left: 10px;
                }
                .setting-content {
                    margin-left: 14px;
                }
            }
        }
        
        .theme {
            display: flex;
            justify-content: space-between;
            .toggle {
                margin-right: 20px;
                display: flex;
                align-items: center;
                .setting-content {
                    margin-right: 4px;
                }
            }
        }
    }

</style>