<template>
  <div class="top-tab">
    <van-nav-bar fixed z-index="100" :title="home.activedName" left-text="" left-arrow
      @click-left="showPopup">
      <template #right>
        <!-- <van-icon name="wap-nav" /> -->
        <van-icon name="search" size="18" />
      </template>
    </van-nav-bar>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import {log} from '../utils/log'
export default {
    name: "toptab",
    data() {
        return {

        };
    },
    created() {
        let path = this.$route.path
        log('path. ', path)
        // this.setState({'path': path})
        let arr = [
            { icon: 'wap-home-o', setName: '首页', path: '/home',},
            { icon: 'apps-o', setName: '频道', path: '/channel',},
            { icon: 'tv-o', setName: '平台', path: '/platform',},
            { icon: 'like-o', setName: '我的关注', path: '/youlike',},
            { icon: 'clock-o', setName: '历史观看', path: '/history',},
            { icon: 'setting-o', setName: '设置', path: '/setting',},
        ]
        // const result = arr.forEach(item => {
        //     if (item.path === path) {
        //         activedName = item.activedName
        //     }
        // })
        let result = arr.filter(item => item.path === path)
        let activedName = result[0].setName
        // log('tresultis. ', result)
        this.setState({'activedName': activedName})
    },
    computed: {
        ...mapState({
            home: state => state.home
        }),
        // carXianLen() {
        //     return (
        //         this.completed.length +
        //         this.complet.length +
        //         this.processing.length
        //     );
        // }
    },
    methods: {
        ...mapMutations(["setState"]),
        showPopup() {
          // log('< click ')
            this.setState( {'isShowSetPage': !this.home.isShowSetPage})
          // log('< click end ')
        },
    },
}
</script>

