export default {
  state: {
    //state
    isLogin: "false",
    data: {
      // username: "",
      // passwd: ""
    }
  },
  mutations: {
    isLoginChange(state) {
      state.isLogin = true;
    },
    setLogin(state, data) {
      //这里的state对应着上面这个state
      state.data = Object.assign(state.data, data);
      //你还可以在这里执行其他的操作改变state
    }
  }
};
